'use client';

import React, { useEffect, useState } from 'react';
import { cn } from '@/lib/utils';

// Brand Colors for the "Planets"
const BRANDS = [
  { name: 'SportPesa', color: 'bg-blue-600', label: 'SP' },
  { name: 'SportyBet', color: 'bg-red-600', label: 'SB' },
  { name: 'Mozzart',   color: 'bg-yellow-500', label: 'MZ' },
  { name: 'Betika',    color: 'bg-green-600', label: 'BK' },
];

export default function CosmicHero() {
  const [rotation, setRotation] = useState(0);

  useEffect(() => {
    const handleScroll = () => {
      // Rotate based on scroll position (speed factor 0.2)
      setRotation(window.scrollY * 0.2);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <div className="relative w-full h-[600px] flex items-center justify-center overflow-hidden">
      
      {/* 1. THE SUPERBEING (Center) */}
      {/* Replace 'src' with your actual Dr. Manhattan transparent PNG later */}
      <div className="relative z-10 w-64 h-96 flex items-center justify-center">
        {/* Placeholder for the entity - Glowing Silhouette */}
        <div className="absolute inset-0 bg-cyan-500/20 blur-[80px] rounded-full animate-pulse" />
        <div className="w-full h-full bg-gradient-to-t from-cyan-900 via-cyan-500 to-white opacity-80" 
             style={{ 
               clipPath: 'polygon(20% 0%, 80% 0%, 100% 20%, 100% 100%, 0% 100%, 0% 20%)',
               maskImage: 'linear-gradient(to bottom, black 50%, transparent 100%)' 
             }} 
        >
           {/* Visual cues for a "body" */}
           <div className="absolute top-10 left-1/2 -translate-x-1/2 w-20 h-20 bg-white/90 blur-sm rounded-full shadow-[0_0_50px_cyan]" />
           <div className="absolute top-32 left-1/2 -translate-x-1/2 w-4 h-4 bg-black rounded-full text-[8px] flex items-center justify-center font-bold">H</div>
        </div>
      </div>

      {/* 2. THE ORBIT RINGS (Visual Guide) */}
      <div className="absolute border border-cyan-500/10 rounded-full w-[400px] h-[400px] animate-spin-slow" />
      <div className="absolute border border-purple-500/10 rounded-full w-[600px] h-[600px]" />

      {/* 3. THE PLANETS (Rotating Logos) */}
      <div 
        className="absolute inset-0 flex items-center justify-center pointer-events-none transition-transform duration-75 ease-out"
        style={{ transform: `rotate(${rotation}deg)` }}
      >
        {BRANDS.map((brand, i) => {
          // Math to position them in a circle
          const angle = (i * 360) / BRANDS.length;
          const radius = 220; // Distance from center
          
          return (
            <div
              key={brand.name}
              className="absolute flex flex-col items-center justify-center"
              style={{
                transform: `rotate(${angle}deg) translate(${radius}px) rotate(-${angle + rotation}deg)` // Counter-rotate to keep text upright
              }}
            >
              {/* The Planet Sphere */}
              <div className={cn("w-16 h-16 rounded-full shadow-[0_0_30px_rgba(0,0,0,0.5)] flex items-center justify-center text-white font-bold text-xl border-2 border-white/10 backdrop-blur-md", brand.color)}>
                {brand.label}
              </div>
              {/* The Label */}
              <div className="mt-2 px-2 py-1 bg-black/50 rounded text-[10px] text-gray-300 uppercase tracking-widest backdrop-blur-sm border border-gray-800">
                {brand.name}
              </div>
            </div>
          );
        })}
      </div>
      
    </div>
  );
}