'use client';

import React, { useEffect, useState } from 'react';
import { supabase } from '@/lib/supabase';
import { Trophy, Users, TrendingUp, Activity } from 'lucide-react';
import { cn } from '@/lib/utils';

export default function LiveIntel() {
  const [recentWins, setRecentWins] = useState<any[]>([]);
  const [topAgents, setTopAgents] = useState<any[]>([]);

  useEffect(() => {
    async function fetchData() {
      // 1. Get Successful Cycles (Mocking data structure for MVP if DB is empty)
      const { data: cycles } = await supabase
        .from('cycles')
        .select('*')
        .eq('status', 'success')
        .order('id', { ascending: false })
        .limit(3);
      
      // If no real wins yet, use demo data to make the landing page look good
      if (!cycles || cycles.length === 0) {
        setRecentWins([
          { name: 'Titan Cycle (Demo)', target_desc: '13/13 Jackpot', wins: 'KES 12M' },
          { name: 'Hunter Alpha (Demo)', target_desc: '15/17 Bonus', wins: 'KES 850K' }
        ]);
      } else {
        setRecentWins(cycles);
      }

      // 2. Get "Top Agents" (Just grabbing random profiles for now)
      const { data: profiles } = await supabase
        .from('profiles')
        .select('email, credits')
        .limit(5);

      if (profiles) setTopAgents(profiles);
    }
    fetchData();
  }, []);

  return (
    <div className="h-full flex flex-col justify-center space-y-6 max-w-md mx-auto">
      
      {/* HEADER */}
      <div className="flex items-center space-x-2 mb-4">
        <span className="relative flex h-3 w-3">
          <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-green-400 opacity-75"></span>
          <span className="relative inline-flex rounded-full h-3 w-3 bg-green-500"></span>
        </span>
        <h3 className="text-sm font-bold text-green-400 tracking-widest uppercase">Live Network Feed</h3>
      </div>

      {/* CARD 1: CYCLE VICTORIES */}
      <div className="bg-black/40 border border-gray-800 rounded-xl p-6 backdrop-blur-md">
        <div className="flex items-center space-x-3 mb-4 text-cyan-400">
          <Trophy className="w-5 h-5" />
          <h4 className="font-bold">Recent Cycle Hits</h4>
        </div>
        <div className="space-y-4">
          {recentWins.map((win, i) => (
            <div key={i} className="flex justify-between items-center border-b border-gray-800 pb-2 last:border-0 last:pb-0">
              <div>
                <p className="text-white font-bold text-sm">{win.name}</p>
                <p className="text-xs text-gray-500">{win.target_desc}</p>
              </div>
              <div className="text-right">
                <span className="bg-green-900/30 text-green-400 text-xs px-2 py-1 rounded border border-green-900">
                  {win.wins || 'WINNER'}
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* CARD 2: TOP AGENTS */}
      <div className="bg-black/40 border border-gray-800 rounded-xl p-6 backdrop-blur-md">
        <div className="flex items-center space-x-3 mb-4 text-purple-400">
          <Users className="w-5 h-5" />
          <h4 className="font-bold">Active Agents</h4>
        </div>
        <div className="space-y-3">
          {topAgents.map((agent, i) => {
            // Derive a fake username from email
            const codename = agent.email.split('@')[0].toUpperCase();
            return (
              <div key={i} className="flex items-center justify-between">
                <div className="flex items-center space-x-3">
                  <div className="w-8 h-8 rounded-full bg-gray-800 flex items-center justify-center text-[10px] font-bold text-gray-400 border border-gray-700">
                    {codename.slice(0, 2)}
                  </div>
                  <div>
                    <p className="text-sm text-gray-300 font-mono">Agent {codename}</p>
                    <p className="text-[10px] text-gray-500">Active in last 24h</p>
                  </div>
                </div>
                <div className="flex items-center text-xs text-purple-300">
                  <Activity className="w-3 h-3 mr-1" />
                  Online
                </div>
              </div>
            );
          })}
          {topAgents.length === 0 && <p className="text-xs text-gray-500">Scanning network nodes...</p>}
        </div>
      </div>

      {/* STATS ROW */}
      <div className="grid grid-cols-2 gap-4">
        <div className="bg-gray-900/50 p-4 rounded-xl border border-gray-800 text-center">
          <p className="text-2xl font-black text-white">98.4%</p>
          <p className="text-[10px] text-gray-400 uppercase">Uptime</p>
        </div>
        <div className="bg-gray-900/50 p-4 rounded-xl border border-gray-800 text-center">
          <p className="text-2xl font-black text-white">12.5x</p>
          <p className="text-[10px] text-gray-400 uppercase">ROI Target</p>
        </div>
      </div>

    </div>
  );
}